<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <h4>Hi <span class="text-uppercase font-weight-bold">{{ currentUser.username }}</span>! <small> Your wallet credit balance:</small></h4>
        <br>
        <div class="text-xs-center">
          <h1 class="text-xs-center font-weight-bold">{{ currentUser.wallet.ccy }} {{ currentUser.wallet.value | 2decimal }}</h1>
          <button class="btn btn-primary btn-xl" @click="gotoTopUpWallet">
            <i class="ion-ios-plus-outline"></i><span>&nbsp;Top Up Balance</span>
          </button>
        </div>   
        <br>     
        <br>   
      </div>
    </div>
    <div class="row">
      <!-- <div class="col-xs-4 text-xs-center">
          <button class="btn btn-outline-primary btn-xl btn-block" @click="gotoLastTransactions">
            <i class="ion-bag"></i><span></span>
          </button>      
      </div> -->
      <div class="col-xs-6 text-xs-center">
          <button class="btn btn-outline-primary btn-md btn-block" @click="gotoInbox">
            <h3><i class="ion-android-mail"></i><span></span></h3>
          </button>      
      </div>
      <div class="col-xs-6 text-xs-center">
          <button class="btn btn-outline-primary btn-md btn-block" @click="gotoSettings">
            <h3><i class="ion-gear-b"></i><span></span></h3>
          </button>      
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <p>Last transactions:</p>
          <table class="table">
            <thead>
              <tr>
                <th scope="col">Merchant</th>
                <th scope="col">Amount</th>
                <th scope="col">Date Time</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(transaction, idx) in currentUser.lastTransaction" :key="idx">
                <td @click="gotoOrderDetails(transaction.id)">
                  <span v-bind:style="{ color: statusToColor('SUCCESS'), 'font-weight': isBold('WAITING') }">
                    <span class="currentOrder">Current Order</span><br>
                    <i class="ion-arrow-right-b"></i> &nbsp;&nbsp;
                    {{ transaction.merchant }}
                  </span>
                </td>
                <td @click="gotoOrderDetails(transaction.id)">SGD {{ transaction.value | 2decimal }}</td>
                <td @click="gotoOrderDetails(transaction.id)">{{ transaction.dateTime | formatDate }}</td>
              </tr>
            </tbody>
          </table>
        <hr>
      </div>
    </div>
    <!-- <div class="row">
      <div class="col-xs-12">
        <div class="text-xs-center">
          <br>
          <router-link
            class="btn btn-lg btn-block"
            exact
            :to="{ name: 'settings' }"
          >
            <button class="btn btn-primary btn-xl">
              <i class="ion-gear-a"></i>&nbsp;Settings
            </button>
          </router-link>
        </div>
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { CHECK_AUTH } from "@/store/actions.type";

export default {
  name: "viewWallet",
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters(["currentUser"])
  },
  methods: {
    statusToColor (status = '') {
      switch(status){
        case 'SUCCESS':
          return '#ffba00'
        case 'FAILED':
          return '#ff0000'
        case 'WAITING':
          return '#555'
        default:
          return '#000'
      }
    },

    isBold (status = '') {
      switch(status){
        case 'WAITING':
          return 'bold'
        default:
          return 'normal'
      }
    },

    gotoTopUpWallet () {
      this.$router.push({ name: "walletTopUp"})
    },

    gotoOrderDetails (orderId) {
      this.$router.push({ name: "orderDetails", params: { orderId: orderId }})
    },

    gotoLastTransactions () {
      alert('Feature work in progress')
      // this.$router.push({ name: "inbox", })
    },

    gotoInbox () {
      this.$router.push({ name: "inbox", })
    },

    gotoSettings () {
      this.$router.push({ name: "settings", })
    },
  },

  created() {
    this.$store.dispatch(CHECK_AUTH, true);
  }
};
</script>

<style scoped>
.successTx{
  color: #ffba00;
}
.processingTx{
  color: #555;
}
.failedTx{
  color: #ff0000;
}
.currentOrder{
  background-color: green;
  color: white;
  padding: 1px 3px;
}
</style>


