<template>
	<div class="container">
		<div v-if="isPageLoading" class="row">
			<div class="col-xs-12">
				Loading {{ merchant.name }}'s data
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div class="col-xs-12">
				<img class="img-fluid merchant-header" :src="merchant.image" />
				<h2><b>{{ merchant.name }}</b></h2>
				<p>{{ merchant.address }}</p>
				<hr>
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div v-ripple class="col-xs-4 text-xs-center" v-bind:class="{ mainColor: isMenuSelected }" @click="changeView('MENU')">
				<h4 class="text-xs-center font-weight-bold">MENU</h4>
			</div>
			<div v-ripple class="col-xs-4 text-xs-center" v-bind:class="{ mainColor: isLoyaltySelected }" @click="changeView('LOYALTY')">
				<h4 class="text-xs-center font-weight-bold">REWARDS</h4>
			</div>
			<div v-ripple class="col-xs-4 text-xs-center" v-bind:class="{ mainColor: isAboutSelected }" @click="changeView('ABOUT')">
				<h4 class="text-xs-center font-weight-bold">ABOUT</h4>
			</div>
			<!-- <div v-ripple class="col-xs-6 text-xs-center" v-bind:class="{ mainColor: isMenuSelected }" @click="changeView('MENU')">
				<h4 class="text-xs-center font-weight-bold">MENU</h4>
			</div>
			<div v-ripple class="col-xs-6 text-xs-center" v-bind:class="{ mainColor: isAboutSelected }" @click="changeView('ABOUT')">
				<h4 class="text-xs-center font-weight-bold">ABOUT</h4>
			</div> -->
		</div>
		<div v-if="!isPageLoading" class="row">
      <transition name="component-fade" mode="out-in" :duration="{ enter: 188, leave: 200 }">
				<div class="col-xs-12" v-if="isMenuSelected">
					<hr class="mb-0">
					<div classs="categoryList" v-for="(category, idx) in merchant.menu" :key="idx">
						<MenuCategory class="menuCategory" :menuCategory='category'></MenuCategory>
						<hr class="margin0">
					</div>
				</div>
			</transition>
      <transition name="component-fade" mode="out-in" :duration="{ enter: 188, leave: 200 }">
				<div class="col-xs-12" v-if="isAboutSelected">
					<hr>
					<br>
					<h5><b>DESCRIPTION</b></h5>
					<p class="twentySizePtHeight">
						{{ merchant.about }}
					</p><br>
					
					<br>
					<h5 v-if="merchant.address_line1"><b>ADDRESS</b></h5>
					<p class="twentySizePtHeight" v-if="merchant.address_line1">
						{{ merchant.address_line1 }}<br>
						{{ merchant.address_line2 }}<br>
						{{ merchant.postal_code }}<br>
						{{ merchant.cityCountry }}
					</p>
					<hr v-if="merchant.contact_no_1">
					
					<br>
					<h5 v-if="merchant.contact_no_1"><b>CONTACT</b></h5>
					<p class="twentySizePtHeight" v-if="merchant.contact_no_1">
						{{ merchant.contact_no_1 }}
					</p>
				</div>
			</transition>
      <transition name="component-fade" mode="out-in" :duration="{ enter: 188, leave: 200 }">
				<div class="col-xs-12" v-if="!isAuthenticated && isLoyaltySelected">
					<hr><br>
					<h3>
						Your rewards
					</h3>
					<div class="progressBar">
						<div>
							<b>&nbsp;&nbsp;0%</b>
						</div>
					</div><br>
					<h4>
						<router-link :to="{ name: 'login', }">
							Login to begin your rewarding retail experiences
						</router-link>
					</h4>
				</div>
			</transition>
      <transition name="component-fade" mode="out-in" :duration="{ enter: 188, leave: 200 }">
				<div class="col-xs-12" v-if="isAuthenticated && isLoyaltySelected">
					<hr><br>
					<h4 v-if="merchant.loyalty.target.loyaltyTimeWindow <= 0">This merchant is not participating in giving out rewards</h4>
					<div v-if="merchant.loyalty.target.loyaltyTimeWindow > 0">
						<h3>
							Your rewards this 
							<span v-if="merchant.loyalty.target.loyaltyTimeWindow == 7">week</span>
							<span v-if="merchant.loyalty.target.loyaltyTimeWindow == 30">month</span>
						</h3>
						<div class="progressBar">
							<div class="progressBarValue" v-if="merchant.loyalty.achieved >= 0.01" :style="{ width: Math.min(parseInt(Math.floor(merchant.loyalty.achieved /  merchant.loyalty.targetloyaltyTarget) * 100), 100) + '%'}">
								<b>X%&nbsp;&nbsp;</b>
							</div>
							<div v-if="merchant.loyalty.achieved <= 0">
								<b>&nbsp;&nbsp;0%</b>
							</div>
						</div>
					</div>
					<!-- <br>
					<h4>X to go for your free boba</h4> -->
					<br>
					<hr>
					<h3>Active vouchers</h3>
					<div v-if="isAuthenticated">
						<h4 v-if="merchant.vouchers.length <= 0">No active vouchers found</h4>
						<div v-if="merchant.vouchers.length > 0">
							<div class="voucherDetails" v-for="(voucher, idx) in merchant.vouchers" :key="idx" @click="applyVoucher(voucher)">
								<h5><b>{{ voucher.name }}</b></h5>
								<p>
									<small>
										Expired at: Monday, Octorber 5th, 2019 (feature Not implemented)
									</small>
								</p>
								{{ voucher.description }}
							</div>
						</div>
					</div>
				</div>
			</transition>
		</div>
	</div>
</template>

<script>
import { FETCH_MERCHANT } from "@/store/actions.type";

import { REMOVE_TEMP_ITEM } from "@/store/mutations.type";
import { SWITCH_OVERLAY_LOADER } from "@/store/mutations.type";

import { mapGetters } from "vuex";
import store from "@/store";

import MenuCategory from "@/components/MenuCategory";

export default {
  name: "viewMerchant",
  components: {
    MenuCategory
  },
  data() {
	  return {
			isPageLoading: false,
			isMenuSelected: true,
			isAboutSelected: false,
			isLoyaltySelected: false,
	  }
	},
	methods: {
		changeView (viewName) {
			this.isMenuSelected = false;
			this.isAboutSelected = false;
			this.isLoyaltySelected = false;

			if(viewName == "MENU"){
				this.isMenuSelected = true;

			}if(viewName == "ABOUT"){
				this.isAboutSelected = true;

			}if(viewName == "LOYALTY"){
				this.isLoyaltySelected = true;
			}

		},

		applyVoucher(voucherObj) {
			// alert('')
					
			this.$swal.fire({
				type: 'info',
				text: 'Apply this voucher at the checkout page',
				showConfirmButton: false,
				showCloseButton: true,
			})
						
			console.log('Voucher clicked', voucherObj)
		},
		
	},
  computed: {
		...mapGetters(["merchant", "isAuthenticated"]),
	},
	// props: {
	// 	merchant: {
	// 		type: Object,
	// 		required: true
	// 	}
	// },
  beforeRouteEnter(to, from, next) {
		// console.log("Checking merchant in memory")
		// console.log(store.state)

		if(store.state.merchant.merchant){
			if(to.params.merchantId == store.state.merchant.merchant.friendlyId){
				// console.log("Merchant same, data not retrieved")
				// console.log(store.state.merchant.merchant)

				if(!store.state.merchant.merchant.currentlyOp){
					alert(store.state.merchant.merchant.name + ' @ ' + store.state.merchant.merchant.address + ' is closed now')
					
        
					// let toast = store.toasted.show(store.state.merchant.merchant.name + ' @ ' + store.state.merchant.merchant.address + ' is closed now', { 
					// 	theme: "outline", 
					// 	position: "bottom-center", 
					// 	duration : 2000,
					// });

				}

				store.commit(REMOVE_TEMP_ITEM);
				
				next();
				
				return;
			}
		}

		// if(to.params.merchantId == store.state.merchant.merchant.friendlyId){
		// 	console.log("Merchant same, data not retrieved")
		// 	console.log(store.state.merchant.merchant)
		// 	next();
		// }else{
		// 	console.log("Merchant not found, retrieving merchant data")
		// }
		

		

		Promise.all([
			store.commit(SWITCH_OVERLAY_LOADER, { root: true }),
			store.dispatch(FETCH_MERCHANT, to.params.merchantId),
			store.commit(SWITCH_OVERLAY_LOADER, { root: true }),
		]).then(() => {
			store.commit(REMOVE_TEMP_ITEM);
			next();
		});

		

  },
}
</script>

<style scoped>
.merchant-header{
	object-fit: cover;
	width:175px;
	height:175px;
	border-radius: 8px;
}

h2{
	margin-top: 10px;
}
.mainColor{
	color: #ffba00;
}
p{
	margin-top: -10px;
}
.margin0{
	margin:0
}
.mb-0{
	margin-bottom: 0;
}
.menuCategory{
	margin-top: 5px;
	
	/* border: 1px solid #ffba00;
	border-radius: 25px; */
}
.twentySizePtHeight{
	line-height: 26pt;
}

.progressBar{
	width: 100%;
	border: 1px solid #ffba00;
	background-color: #fffaeb;
	border-radius: 25px;
	height: 35px;
	padding: 3.8px;
	font-size: 15px;
}

.progressBar > .progressBarValue {
	/* width: 20%; */
	height: 100%;
	background-color: rgba(255, 186, 0, 1);
	border: 1px solid #ffba00;
	border-radius: 25px;
	text-align: right;
}

.voucherDetails{
	border: 1px solid #ffba00;
	border-radius: 15px;
	padding: 15px;
	margin-bottom: 15px;
}

.voucherDetails > h4{
	color: #ffba00;
}

.voucherDetails{
	color: #555555;
}

.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .2s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

</style>


